.example-viewer {
  display: block;
  margin-bottom: 2rem;
}

.example-viewer-card {
  background: var(--surface-card);
  border: 1px solid var(--border-color);
  border-radius: var(--border-radius-lg);
  overflow: hidden;
  transition: border-color var(--transition-fast), box-shadow var(--transition-fast);
}
.example-viewer-card:hover {
  border-color: var(--border-color-hover);
}

.example-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.75rem 1rem;
  background: var(--surface-elevated);
  border-bottom: 1px solid var(--border-color);
}

.example-title {
  font-family: var(--font-heading);
  font-size: var(--text-base);
  font-weight: 600;
  color: var(--text-primary);
}
.example-title h1, .example-title h2, .example-title h3, .example-title h4, .example-title h5, .example-title h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}

.example-actions {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.action-btn {
  color: var(--text-muted);
  transition: all var(--transition-fast);
}
.action-btn:hover {
  color: var(--text-primary);
  background: var(--sidebar-link-hover);
}
.action-btn.active {
  color: var(--c3-primary);
  background: var(--sidebar-link-active);
}
.action-btn .mat-icon {
  font-size: 20px;
  width: 20px;
  height: 20px;
}

.example-preview {
  padding: 1.5rem;
  background: var(--surface-page);
}
.example-preview > *:last-child {
  margin-bottom: 0;
}

.example-source {
  border-top: 1px solid var(--border-color);
  background: var(--code-bg);
}
.example-source .mat-mdc-tab-group .mat-mdc-tab-header {
  background: rgba(0, 0, 0, 0.2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.example-source .mat-mdc-tab-group .mat-mdc-tab {
  color: var(--code-text) !important;
  opacity: 0.7;
}
.example-source .mat-mdc-tab-group .mat-mdc-tab.mdc-tab--active {
  opacity: 1;
}
.example-source .mat-mdc-tab-group .mat-mdc-tab-body-wrapper {
  background: var(--code-bg);
}
.example-source .mat-mdc-tab-group .mdc-tab-indicator__content--underline {
  border-color: var(--c3-primary) !important;
}

.code-container {
  padding: 0;
}
.code-container pre, .code-container textarea {
  margin: 0;
  padding: 1rem;
  background: transparent !important;
  color: var(--code-text);
  font-family: var(--font-mono);
  font-size: var(--text-sm);
  line-height: 1.6;
  white-space: pre;
  overflow-x: auto;
  border: none;
  resize: none;
  width: 100%;
}
.code-container pre:focus, .code-container textarea:focus {
  outline: none;
}
.code-container .hljs {
  background: transparent !important;
  padding: 1rem;
}

.animate-fadeInUp {
  animation: fadeInUp 0.2s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@media (max-width: 640px) {
  .example-preview {
    padding: 1rem;
  }
  .code-container pre, .code-container textarea {
    font-size: var(--text-xs);
  }
}
